.hamburger-menu {
  display: block;
  width: 25px;
  height: 3px;
  margin-bottom: 5px;
  background-color: #222222;
  position: relative; }

.nav {
  cursor: pointer;
  width: 100%; }

.nav--closed {
  position: absolute;
  left: 270px;
  top: 26px; }

.nav--opened .hamburger-menu {
  display: none; }

.nav--opened:before,
.nav--opened:after {
  content: "";
  position: absolute;
  width: 25px;
  height: 3px;
  background-color: #222222;
  top: 33px;
  left: 270px; }

.nav--opened:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

.nav--opened:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg); }

.nav-list {
  width: 100%; }

.nav--opened + .nav-list {
  display: -webkit-box;
